  background-image: linear-gradient(rgba(0, 0, 0, 0.10), rgba(0, 0, 0, 0.20)), url('/assets/img/background_words.jpg');
  grid-gap: 1.6em;
  gap: 1.6em;
}

body.dark .parallax-container2 {
